import React, { useState } from 'react';
import { Link, useSearchParams } from "react-router-dom";
import { Table, Alert } from 'react-bootstrap'

const DownloadFiles = () => {

    const [searchParams] = useSearchParams();
    const [downloadSuccess, setDownloadSuccess] = useState(false);
    const category = searchParams.get('category');

    const generateFileList = (categoryDir, extensionList) => {
        const files = []
        extensionList.forEach((ext) => {
            const fileName = `sample_${ext}.${ext}`;
            const downloadLink = `../downloadFiles/${categoryDir}/${fileName}`;
            const fileDescription = `Sample ${ext.toUpperCase()} file`;

            const file = {
                name: fileName,
                extension: ext,
                description: fileDescription,
                downloadLink,
            }
            files.push(file);
        });
        return files;
    }

   
    // retrieve files depending on category
    // get this file list... if(category === encodeURIComponent('Forcepoint ZT Supported Files')) ...
    
    let files = [];
    let description = "";
    
    if(category === 'Forcepoint ZT Supported Files') {
        const extensionList = ['bmp', 'csv', 'doc', 'docm', 'docx', 'gif', 'jp2', 'jpg', 'json', 'jxr', 'odp', 'ods', 'odt', 'pdf', 'png', 'ppt', 'pptm', 'pptx', 'tiff', 'vsdx', 'webp', 'xls', 'xlsb', 'xlsm', 'xlsx', 'xml', 'zip'];
        const fileList = generateFileList('fztcdr_supportedfiles', extensionList);
        files = [...fileList];
        description = "Description for Forcepoint ZT Supported Files";
    }
    if(category === 'Forcepoint ZT UnSupported Files') {
        const extensionList = ['dotm', 'mov', 'mp3', 'mp4', 'potm', 'ppsm', 'rar', 'svg', 'tar', 'txt', 'wav', 'xlt'];
        const fileList = generateFileList('fztcdr_unsupportedfiles', extensionList);
        files = [...fileList];
        description = "Description for Forcepoint ZT UnSupported Files";
    }
    if(category === 'Opswat ZT Supported Files') {
        const extensionList = ['7z', 'ai', 'aiff', 'apng', 'avi', 'bmp', 'bz2', 'csv', 'cur', 'doc', 'docm', 'docx', 'dot', 'dotm', 'dotx', 'DWF', 'dxf', 'exe', 'gif', 'heic', 'hta', 'htm', 'html', 'hwp', 'ics', 'jp2', 'jpg', 'json', 'jxr', 'm4a', 'mht', 'mov', 'mp3', 'mp4', 'mpeg', 'nef', 'odp', 'ods', 'odt', 'ogg', 'opus', 'otp', 'ots', 'ott', 'pdf', 'png', 'pot', 'potm', 'potx', 'pps', 'ppsm', 'ppsx', 'ppt', 'pptm', 'pptx', 'rar', 'rtf', 'svg', 'tar', 'tiff', 'tsv', 'txt', 'vsdm', 'vsdx', 'vssm', 'vssx', 'vstm', 'vstx', 'vsx', 'vtx', 'wav', 'webm', 'webp', 'wma', 'wmf', 'wmv', 'xls', 'xlsb', 'xlsm', 'xlsx', 'xlt', 'xltm', 'xltx', 'xml', 'zip'];
        const fileList = generateFileList('opswat_supportedfiles', extensionList);
        files = [...fileList];
        description = "Description for Opswat ZT Supported Files";
    }
    if(category === 'Opswat ZT UnSupported Files') {
        const extensionList = ['3gp', 'aac', 'crt', 'djvu', 'flv', 'pem'];
        const fileList = generateFileList('opswat_unsupportedfiles', extensionList);
        files = [...fileList];
        description = "Description for Opswat ZT UnSupported Files";
    }
    if(category === 'Trellix AV Malicious Files') {
        const fileList = [
            { name: 'test_file', extension: '', description: 'Sample file', downloadLink: '../downloadFiles/trellixav_files/test_file', },
            { name: 'test_file.html', extension: 'html', description: 'Sample HTML file', downloadLink: '../downloadFiles/trellixav_files/test_file.html', },
        ]
        files = [...fileList];
        description = "Description for Trellix AV  Malicious Filess";
    }
    if(category === 'Clam AV Malicious Files') {
        const fileList = [
            { name: 'sample_html.html', extension: 'html', description: 'Sample HTML file', downloadLink: '../downloadFiles/clamav_files/sample_html.html', },
            { name: 'file_1', extension: '', description: 'Sample file', downloadLink: '../downloadFiles/clamav_files/file_1', },
            { name: 'file_2', extension: '', description: 'Sample file', downloadLink: '../downloadFiles/clamav_files/file_2', },
            { name: 'file_3', extension: '', description: 'Sample file', downloadLink: '../downloadFiles/clamav_files/file_3', },
            { name: 'file_4', extension: '', description: 'Sample file', downloadLink: '../downloadFiles/clamav_files/file_4', },
        ]
        files = [...fileList];
        description = "Description for Clam AV  Malicious Files";
    }
    if(category === 'Fab Menu Test') {
        const fileList = [
            { name: 'demo.html', extension: 'html', description: 'Sample HTML file', downloadLink: '../downloadFiles/fab_menu/demo.html', },
            { name: 'demo2.html', extension: 'html', description: 'Sample HTML file', downloadLink: '../downloadFiles/fab_menu/demo2.html', },
            { name: 'filedownload.pdf', extension: 'pdf', description: 'Sample PDF file', downloadLink: '../downloadFiles/fab_menu/filedownload.pdf', }
        ]
        files = [...fileList];
        description = "Description for Fab Menu Test Files";
    }

    return (
        <div className='mt-5'>
            <h3>{category}</h3>
            <h5>{description}</h5>


            <div className='d-flex justify-content-center'>
                <div style={{width: '60%'}}>
                    {downloadSuccess && 
                    <div className='topcorner'>
                        <Alert className='alert' variant="success" onClose={() => setDownloadSuccess(false)} dismissible >  
                        File Downloaded Successfully! </Alert>
                    </div>}
                    <p style={{textAlign: 'left'}}>{`TOTAL FILES: ${files.length}`}</p>
                    <Table striped bordered hover> 
                        <thead>
                            <tr>
                                <th>Extension</th>
                                <th>Description</th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            { files.map((file, index) => (
                                <tr key={index}>
                                    <td>{file.extension}</td>
                                    <td>{file.description}</td>
                                    <td><Link to={file.downloadLink} target="_blank" onClick={()=>setDownloadSuccess(true)} download>Download</Link></td>
                                </tr>
                            ))}
                        </tbody>
                    </Table>
                </div>
            </div>
        </div>
    )
}

export default DownloadFiles;