import React, { useState } from 'react';
import { Editor } from '@tinymce/tinymce-react';

const Notepad = () => {

    const [contentL, setContentL] = useState('');
    const [contentR, setContentR] = useState('');
    

    const handleEditorChangeL = (content, editor) => { setContentL(content); };
    const handleEditorChangeR = (content, editor) => { setContentR(content); };

    return (
        <div className='mt-5'>
            <h3 className='text-center mb-4'>Notepad</h3>
            <div className='d-flex justify-content-center'>
                <div style={{width: '90%'}}>
                    <div style={{display: 'flex'}}>
                        <div style={{flex: 1, margin:'5px'}}>
                            <h5>Editor Left</h5>
                            <Editor
                                apiKey='fpy0nbwkthd62zp5jfc5pvh2ilq8m1kr207ukqcjdqiqmow0'
                                value={contentL}
                                init = {{
                                    height: 300,
                                    menubar: false,
                                    plugins: 'anchor autolink charmap codesample emoticons image link lists media searchreplace table visualblocks wordcount checklist mediaembed casechange export formatpainter pageembed linkchecker a11ychecker tinymcespellchecker permanentpen powerpaste advtable advcode editimage tableofcontents footnotes mergetags autocorrect typography inlinecss',
                                    toolbar: 'undo redo | blocks fontfamily fontsize | bold italic underline strikethrough | link image media table mergetags | addcomment showcomments | spellcheckdialog a11ycheck typography | align lineheight | checklist numlist bullist indent outdent | emoticons charmap | removeformat',
                                }}
                                onEditorChange={handleEditorChangeL}
                            />
                        </div>

                        <div style={{flex: 1, margin:'5px'}}>
                            <h5>Editor Right</h5>
                            <Editor
                                apiKey='fpy0nbwkthd62zp5jfc5pvh2ilq8m1kr207ukqcjdqiqmow0'
                                value={contentR}
                                init = {{
                                    height: 300,
                                    menubar: false,
                                    plugins: 'anchor autolink charmap codesample emoticons image link lists media searchreplace table visualblocks wordcount checklist mediaembed casechange export formatpainter pageembed linkchecker a11ychecker tinymcespellchecker permanentpen powerpaste advtable advcode editimage tableofcontents footnotes mergetags autocorrect typography inlinecss',
                                    toolbar: 'undo redo | blocks fontfamily fontsize | bold italic underline strikethrough | link image media table mergetags | addcomment showcomments | spellcheckdialog a11ycheck typography | align lineheight | checklist numlist bullist indent outdent | emoticons charmap | removeformat',
                                }}
                                onEditorChange={handleEditorChangeR}
                            />
                        </div>

                    </div>
                </div>
            </div>
        </div>
    )
}

export default Notepad;
