import './App.css';
import 'bootstrap/dist/css/bootstrap.css';

import React from 'react';
import {Nav, Navbar} from "react-bootstrap";
import {Routes, Route, Link,} from "react-router-dom";

import Home from './Components/Home.js';
import UploadPage from './Components/Upload.js';
import DownloadPage from './Components/Download.js';
import DownloadFiles from './Components/DownloadFiles';
import Notepad from './Components/Notepad';

function App() {
  return (
    <div className="App">
      <Navbar bg="light" expand="lg" className='navbar' style={{paddingLeft: '3.5rem', paddingRight: '3.5rem'}}>
        {/* <Navbar.Brand href='/' className="ml-3" style={{marginLeft: '5%'}}>Upload/Download</Navbar.Brand> */}
        <Nav.Link as={Link} to="/" className="navbar-brand ml-3"><img src='/forcepoint.svg' height='21rem' alt='forcepoint logo'/></Nav.Link>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="navbar">
          <Nav className="ms-auto">
            <Nav.Link as={Link} to="/" className="ml-3">Home</Nav.Link>
            <Nav.Link as={Link} to="/upload" className="ml-3">Upload</Nav.Link>
            <Nav.Link as={Link} to="/download" className="ml-3">Download</Nav.Link>
            <Nav.Link as={Link} to="/notepad" className="ml-3">Notepad</Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>

      <Routes>
        <Route path="/" element={ <Home/> } />
        <Route path="/upload" element={<UploadPage />} />
        <Route path="/download" element={<DownloadPage />} />
        <Route path="/download/files" element={ <DownloadFiles/> } />
        <Route path="/notepad" element={ <Notepad /> } />
      </Routes>
    </div>
  );
}

export default App;
