import React, { useState } from 'react';
import { CopyToClipboard } from 'react-copy-to-clipboard';

const Home = () => {
    const [copied, setCopied] = useState(false);
    const text = 'Text to be Copied';

    return (
        <div className="d-flex justify-content-center mt-5">
            <div style={{width: '60%'}}>
                <h3>Home</h3>
                <p></p>
                <div className={copied ? 'discount-code discount-applied' : 'discount-code'} >
                    <div className="black-code">{text}</div>
                    {
                        <CopyToClipboard text={text} onCopy={() => setCopied(true)}>
                        {copied ? <div className="discount-copied copy">Copied!</div> : <div className="copy">Copy</div>}
                        </CopyToClipboard>
                    }
                </div>
                <p></p>
                <p className='lead ml-4'>Forcepoint, is an American multinational corporation software company headquartered in Austin, Texas, that develops computer security software and data protection, cloud access security broker, firewall and cross-domain solutions. Forcepoint was founded in 1994 as an information technology reseller called NetPartners.</p>
            </div>
            
        </div>
    )
}

export default Home;