import React from 'react';
import {useNavigate, createSearchParams} from "react-router-dom";

const DownloadPage = () => {

    const navigate = useNavigate();
    const goToFiles = (props) => {
        const query = {
            category: props.title,
        }
        navigate({
            pathname: '/download/files',
            search: `${createSearchParams(query)}`,
        });
    }

    const Card = (props) => {
        return (
          <div className="card" onClick={() => goToFiles(props)}>
            <div className="card__body">
                <h5 className="card__title">{props.title}</h5>
                <p className="card__description">{props.description}</p>
            </div>
          </div>
        );
    }

    return (
        <div className='container mt-5'>
            <h3 className='text-center mb-4'>Download Files</h3>
  
            <div className="wrapper">
                <Card
                    title="Forcepoint ZT Supported Files"
                    description="description 1"
                />
                <Card
                    title="Forcepoint ZT UnSupported Files"
                    description="description 2"
                />
                <Card
                    title="Opswat ZT Supported Files"
                    description="description 3"
                />
                <Card
                    title="Opswat ZT UnSupported Files"
                    description="description 4"
                />
                <Card
                    title="Trellix AV Malicious Files"
                    description="description 5"
                />
                <Card
                    title="Clam AV Malicious Files"
                    description="description 6"
                />
                <Card
                    title="Fab Menu Test"
                    description="description 7"
                />

            </div>
        </div>
    )
}

export default DownloadPage;