import React, { useState } from 'react';
import {Form, Button, Alert} from 'react-bootstrap';


const UploadPage = () => {
    const [selectedFiles, setSelectedFiles] = useState(null);
    const [currentFile, setCurrentFile] = useState(null);
    const [uploadSuccess, setUploadSuccess] = useState(false);

    const selectFile = (event) => {
        setSelectedFiles(event.target.files);
    };


    const upload = () => {
        let currentFile = selectedFiles[0];
        setCurrentFile(currentFile);
        console.log('Uploaded: ', currentFile);
        setUploadSuccess(true);
        setSelectedFiles(null);
    };

    return (
        <div className='container mt-5'>
            {uploadSuccess && 
                <Alert className='alert' variant="success" onClose={() => setUploadSuccess(false)} dismissible >  
                File Uploaded Successfully! </Alert>}

            <h3 className='text-center mb-4'>Upload Files</h3>

            <div className='d-flex justify-content-center'>
                <div style={{width: '60%'}}>
                    <Form>
                        <Form.Group controlId='fileForm' className='mb-4'>
                            <Form.Control type='file' onChange={selectFile}></Form.Control>
                        </Form.Group>
                        <Button onClick={upload} disabled={!selectedFiles}>Upload</Button>
                    </Form>
                </div>
            </div>

            
        </div>
    )
}

export default UploadPage;